/*
 * 	Default theme - Owl Carousel CSS File
 */

$color-base:			$primary;
$color-white:			#FFF;
$color-gray:			#D6D6D6;

//nav

$nav-width: 			32px;
$nav-height: 			32px;
$nav-color: 			#1d1f29;
$nav-color-hover:		$color-white;
$nav-font-size: 		20px;
$nav-rounded: 			3px;
$nav-margin: 			5px;
$nav-padding: 			4px 7px;
$nav-background: 		$color-gray;
$nav-background-hover:	$color-base;
$nav-disabled-opacity: 	0.5;

//dots

$dot-width:				14px;
$dot-height:			14px;
$dot-rounded:			30px;
$dot-margin: 			5px 7px;
$dot-background:		$color-gray;
$dot-background-active:	$color-white;
$dot-border-active:	    $color-base;

@import 'theme';
