
.owl-theme {
	// Styling Next and Prev buttons
	.owl-nav {
		text-align: center;
		-webkit-tap-highlight-color: transparent;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;

		[class*='owl-'] {
			color: $nav-color;
			font-size: $nav-font-size;
			margin: $nav-margin;
			padding: $nav-padding;
			background: $nav-background;
			display: inline-block;
			cursor: pointer;
			border-radius: 50%;
			border: 0;
			width: $nav-width;
			height: $nav-height;
			transition: 0.2s;
			position: absolute;
			&:hover {
				background: $nav-background-hover;
				color:$nav-color-hover;
				text-decoration: none;
			}
		}
		.owl-prev{
			left: 0;
		}
		.owl-next{
			right: 0;
		}
		.disabled {
			opacity: $nav-disabled-opacity;
			cursor: default;
		}
	}

	// Styling dots
	.owl-dots {
		margin-top: 24px;
	}

	.owl-dots {
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		.owl-dot {
			display: inline-block;
			zoom: 1;
			*display: inline;
			border: 0;
			background: transparent;
			span {
				width: $dot-width;
				height: $dot-height;
				margin: $dot-margin;
				background: $dot-background;
				display: block;
				-webkit-backface-visibility: visible;
				transition: 0.2s;
				border-radius: $dot-rounded;
				border: 2px solid transparent;
				position: relative;
				&::after{
					position: absolute;
					content: "";
					top: 2px;
					left: 2px;
					width: 6px;
					height: 6px;
					background-color: $primary;
					border-radius: 50%;
					transform: scale(0);
					transition: 0.2s;
				}
			}
			&.active{
				span {
					background: $dot-background-active;
					border-color: $dot-border-active;
					&::after{
						transform: scale(1);
					}
				}
			}
		}
	}
}
